@charset "UTF-8";
*,
:before,
:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background: #fff;
  font: 400 19px/28px "Fira Sans", sans-serif;
  color: #3d4140;
}

a {
  color: #5a97cb;
  outline: none;
  text-decoration: underline;
}

a:hover,
a:active {
  text-decoration: none;
}

p {
  margin: 0 0 10px;
}

img {
  display: block;
  max-width: 100%;
}

b,
strong {
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fira Sans", sans-serif;
  font-weight: 600;
  color: #0a1828;
  line-height: normal;
  margin: 0 0 15px;
}

h1 {
  font-size: 46px;
  line-height: 59px;
  font-weight: 700;
}

h2 {
  font-size: 40px;
  line-height: 30px;
  font-weight: 700;
}

h3 {
  font-size: 31px;
  line-height: 26px;
}

h4 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 400;
}

h5 {
  font-size: 28px;
  line-height: 30px;
  font-weight: 700;
}

h6 {
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0 0 10px;
}

ul.marker li,
ol.marker li {
  padding-left: 17px;
  position: relative;
}

ul.marker li:before {
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #232625;
  display: block;
  position: absolute;
  left: 0;
  top: 12px;
}

ol.marker li {
  counter-increment: step-counter;
}

ol.marker li:before {
  content: counter(step-counter) ".";
  position: absolute;
  left: 0;
  top: 0;
  font-size: 19px;
  line-height: 28px;
  color: #3d4140;
}

q {
  display: block;
  margin: 0 0 10px;
  padding: 20px 0;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  font-style: italic;
  color: #3d4140;
}

svg {
  display: block;
  overflow: hidden;
}

input,
textarea,
select,
button {
  outline: none;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #888f98;
  opacity: 1;
  /* Хром */
}

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #888f98;
  opacity: 1;
  /* Файерфокс */
}

input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #888f98;
  opacity: 1;
  /* Для старых версий файерфокса */
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #888f98;
  opacity: 1;
  /* едж и эксплорер */
}

input::-ms-clear {
  display: none;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.cover {
  padding: 0 15px;
  position: relative;
  margin: 0 auto;
}
@media screen and (min-width: 480px) {
  .cover {
    min-width: 320px;
  }
}
@media screen and (min-width: 600px) {
  .cover {
    max-width: 570px;
  }
}
@media screen and (min-width: 768px) {
  .cover {
    max-width: 730px;
  }
}
@media screen and (min-width: 990px) {
  .cover {
    max-width: 970px;
  }
}
@media screen and (min-width: 1200px) {
  .cover {
    max-width: 1170px;
  }
}
@media screen and (min-width: 1440px) {
  .cover {
    max-width: 1390px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.wrapper__content {
  flex: 1 0 auto;
}
.wrapper__footer {
  flex: 0 0 auto;
}

.social {
  margin: 0 -5px;
  display: flex;
}
.social__item {
  padding: 0 5px;
}
.social__link {
  display: block;
  height: 29px;
  width: 29px;
}
.social__icon {
  max-width: none;
  width: 100%;
}

.pull {
  flex: 0 0 auto;
  display: flex;
}
@media screen and (min-width: 990px) {
  .pull {
    display: none;
  }
}
.pull__wrapper {
  display: flex;
  cursor: pointer;
  flex: 0 0 auto;
  align-items: center;
}
.pull__icon {
  width: 20px;
  height: 17px;
  flex: 0 0 auto;
  position: relative;
}
.pull__icon div {
  height: 2px;
  width: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  transition: all 200ms ease;
}
.pull__icon div:nth-child(1) {
  top: 0;
}
.pull__icon div:nth-child(2) {
  top: 7px;
}
.pull__icon div:nth-child(3) {
  top: 14px;
}
.pull__text {
  font: 500 17px "Fira Sans", sans-serif;
  color: #fff;
  text-transform: uppercase;
  display: none;
  margin-left: 15px;
}
@media screen and (min-width: 480px) {
  .pull__text {
    display: block;
  }
}

.header__top {
  background: #0a1828;
}
.header__top-wrapper {
  display: flex;
}
.header__top-left {
  display: flex;
  flex: 1 1 auto;
  padding: 18px 0;
  align-items: center;
}
.header__top-right {
  display: flex;
  align-items: center;
  border-left: 1px solid #16283e;
  position: relative;
  padding: 18px 0 18px 12px;
}
.header__top-right:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100000px;
  height: 100%;
  overflow: hidden;
  background: #07101b;
  z-index: 1;
}
.header__top-buttons {
  display: flex;
  align-items: center;
  margin: 0 -10px;
  padding: 0 13px;
  z-index: 2;
}
.header__top-btn {
  padding: 0 10px;
}
.header__top-btn-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  position: relative;
}
.header__top-btn-icon {
  fill: #fff;
}
@media screen and (min-width: 1440px) {
  .header__top-btn-icon {
    margin-right: 10px;
  }
}
.header__top-btn-icon_grey {
  fill: #c4c4c5;
}
.header__top-btn-count {
  display: block;
  position: absolute;
  top: -3px;
  right: -5px;
  border-radius: 13px;
  font: 500 11px/11px "Fira Sans", sans-serif;
  text-transform: uppercase;
  color: #fff;
  padding: 2px;
}
@media screen and (min-width: 1440px) {
  .header__top-btn-count {
    position: relative;
    top: auto;
    right: auto;
    padding: 4px 13px;
    font: 500 15px/19px "Fira Sans", sans-serif;
  }
}
.header__top-btn-count_blue {
  background: #4c8dc8;
}
.header__top-btn-count_red {
  background: #ea5c57;
}
.header__top-btn-count_yellow {
  background: #fbb204;
}
.header__top-user {
  display: flex;
  align-items: center;
  z-index: 2;
  text-decoration: none;
  color: #fff;
  margin-left: 15px;
}
.header__top-user:hover, .header__top-user:active {
  text-decoration: underline;
}
.header__top-user-circle {
  position: relative;
  background: #eaedee;
  height: 27px;
  width: 27px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;
  flex: 0 0 auto;
}
.header__top-user-icon {
  fill: #b2b7b8;
  position: absolute;
  bottom: 0;
  left: 2px;
}
.header__top-user-text {
  display: none;
  font: 400 15px/15px "Fira Sans", sans-serif;
  color: #fff;
}
@media screen and (min-width: 1200px) {
  .header__top-user-text {
    display: block;
  }
}
.header__nav {
  flex: 1 1 auto;
}
.header__nav-list {
  display: none;
  align-items: center;
  margin: 0 -15px;
}
@media screen and (min-width: 990px) {
  .header__nav-list {
    display: flex;
  }
}
.header__nav-item {
  padding: 7px 15px;
}
.header__nav-link {
  display: block;
  font: 400 15px/15px "Fira Sans", sans-serif;
  color: #eeeeee;
  text-decoration: none;
  transition: all 0.3s ease;
}
.header__nav-link:hover, .header__nav-link:active {
  color: #E83F33;
}
.header__social-block {
  margin: 0 20px;
  display: none;
}
@media screen and (min-width: 480px) {
  .header__social-block {
    display: block;
  }
}
.header__main-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 -20px;
}
@media screen and (min-width: 768px) {
  .header__main-wrapper {
    flex-direction: row;
  }
}
.header__logo {
  padding: 18px 20px;
}
.header__logo-link {
  display: block;
  max-width: 224px;
}
.header__logo-img {
  max-width: none;
  width: 100%;
}
.header__buttons {
  display: flex;
  align-items: center;
  padding: 10px 10px 20px 10px;
  order: 1;
}
@media screen and (min-width: 768px) {
  .header__buttons {
    order: 0;
    padding: 0 10px;
  }
}
.header__btn {
  padding: 0 10px;
}
.header__btn-link {
  height: 57px;
  min-width: 57px;
  text-align: center;
  text-decoration: none;
  font: 600 15px/15px "Fira Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  color: #30322e;
  display: block;
  border: 2px solid #e1e4ec;
  border-radius: 2px;
  transition: all 0.3s ease;
}
.header__btn-link:hover, .header__btn-link:active {
  color: #105394;
}
@media screen and (min-width: 1200px) {
  .header__btn-link {
    padding: 0 25px;
    min-width: 167px;
  }
}
.header__btn-link.active {
  background: #e1e4ec;
}
@media screen and (min-width: 768px) {
  .header__btn-link.active {
    height: 113px;
  }
}
.header__btn-content {
  display: inline-block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.header__btn-content_catalog-icon {
  padding-left: 40px;
}
.header__btn-content_catalog-icon:before {
  content: "";
  position: absolute;
  left: 8px;
  top: -20px;
  width: 26px;
  height: 18px;
  background: url(../images/catalog.svg) left top no-repeat;
}
@media screen and (min-width: 1200px) {
  .header__btn-content_catalog-icon:before {
    left: 0;
    top: -2px;
  }
}
.header__btn-content_search-icon {
  padding-left: 24px;
}
.header__btn-content_search-icon:before {
  content: "";
  position: absolute;
  left: 4px;
  top: -19px;
  width: 16px;
  height: 16px;
  background: url(../images/search.svg) left top no-repeat;
}
@media screen and (min-width: 1200px) {
  .header__btn-content_search-icon:before {
    left: 0;
    top: -1px;
  }
}
.header__btn-text {
  display: none;
}
@media screen and (min-width: 1200px) {
  .header__btn-text {
    display: inline-block;
  }
}
.header__btn-text_hulltext {
  display: none !important;
}
@media screen and (min-width: 1440px) {
  .header__btn-text_hulltext {
    display: inline-block !important;
  }
}
.header__main-nav {
  display: none;
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 990px) {
  .header__main-nav {
    display: flex;
  }
}
@media screen and (min-width: 1440px) {
  .header__main-nav {
    padding: 0 20px;
  }
}
.header__main-nav-item {
  padding: 0 10px;
}
@media screen and (min-width: 1440px) {
  .header__main-nav-item {
    padding: 0 20px;
  }
}
.header__main-nav-link {
  display: block;
  text-decoration: none;
  font: 600 17px/17px "FiraSans", sans-serif;
  transition: all 0.3s ease;
  color: #30322e;
}
.header__main-nav-link:hover, .header__main-nav-link:active {
  color: #E83F33;
}
.header__contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 20px;
}
@media screen and (min-width: 768px) {
  .header__contacts {
    padding: 0 20px;
    align-items: flex-end;
  }
}
.header__phone-link {
  display: block;
  text-decoration: none;
  cursor: default;
  font: 600 24px/24px "Fira Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  color: #004b90;
}
@media screen and (min-width: 1440px) {
  .header__phone-link {
    letter-spacing: 0.5px;
  }
}
.header__link {
  font: 400 15px/15px "Fira Sans", sans-serif;
  color: #004b90;
  text-decoration: none;
  border-bottom: 2px dotted;
  transition: all 0.1s ease;
}
.header__link:hover, .header__link:active {
  border-bottom: none;
}